body{
  margin: 0;
  box-sizing: border-box;  
  /* font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"; */
  font-family: 'Roboto', sans-serif;
}
 body,input {
    font-family: "poppins",sans-serif;
  }

 .container{
   position: relative;
   width: 100%;
   min-height: 100vh;
   background-color: #fff;
   overflow: hidden;
 }

 .forms-container{
   position:absolute;
   width: 100%;
   height: 100%;
   top: 0;
   left: 030%;
 }

 form #logo-img{
   margin: auto;
   width: 250px;
   align-self: center;
   margin-top: 20px;
   margin-bottom: 20px;
 }
 .signin-signup{
   position: absolute;
   top: 50%;
   -webkit-transform: translate(-50%,-50%);
           transform: translate(-50%,-50%);
   left: 50%;
   width: 50%;
   transition: 1s 0.7s ease-in-out;
   display: grid;
   grid-template-columns: 1fr;
   z-index: 5;
   
   
 }

 .signin-signup form{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   padding: 0rem 5rem;
   transition: all 0.2s 0.7s;
   overflow: hidden;
   grid-column: 1 / 2;
   grid-row: 1 / 2;
 }

 form.sign-up-form{
   opacity: 0;
   z-index: 1;
 }

 form.sign-in-form{
  z-index: 2;
}

 .title{
   font-size: 2.2rem;
   color: rgba(51, 51, 51, 0.932);
   margin-bottom: 10px ;
 }

 .input-field{
   max-width: 380px;
   width: 100%;
   height: 55px;
   background-color: #f0f0f0;
   margin: 10px 0;
   border-radius: 55px;
   display: grid;
   grid-template-columns: 15% 85%;
   padding: 0 .4rem;
   position: relative;
 }

 .input-field i{
  text-align: center;
  line-height: 55px;
  color: #acacac;
  transition: 0.5s;
  font-size: 1.1rem;
}
.input-field input{
  background: none;
  outline: none;
  border: none;
  line-height: 1;
  font-weight: 600;
  font-size: 1.1rem;
  color: #333;
}

.input-field input::-webkit-input-placeholder{
  color: #aaa;
  font-weight: 500;
}

.input-field input:-ms-input-placeholder{
  color: #aaa;
  font-weight: 500;
}

.input-field input::placeholder{
  color: #aaa;
  font-weight: 500;
}

.button{
width: 150px;
height: 49px;
border: none;
outline: none;
border-radius: 49px;
cursor: pointer;
background-color: #5261ac;
color: #fff;
text-transform: uppercase;
font-weight: 600;
margin: 10px 0;
transition: .5s;
}

.button:hover{
  background-color: #77be44;
}

.panels-container{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.container::before{
  content: "";
  position: absolute;
  width: 2000px;
  height: 2000px;
  border-radius: 50%;
  background-image: linear-gradient(-45deg,#5569dc 0%,#5569dc 100%);
  top: -10%;
  right: 40%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: 3s ease-in;
  z-index: 6;
}

.panel{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 6;
}

.left-panel{
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}

.right-panel{
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}

.panel .content{
  color: #fff;
  transition: -webkit-transform 0.9s ease-in-out;
  transition: transform 0.9s ease-in-out;
  transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
  transition-delay: 0.6s;
}

.panel h3{
  font-weight: 600;
  line-height: 1;
  font-size: 1.5rem;
}

.panel p{
  font-size: 0.95rem;
  padding: 0.7rem 0;
}

.button.transparent{
  margin: 0;
  background: none;
  border: 2px solid #fff ;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}

.right-panel .content,.right-panel .image{
  -webkit-transform: translateX(800px);
          transform: translateX(800px);
}

.image{
  width: 100%;
  transition: -webkit-transform 1.1s ease-in-out;
  transition: transform 1.1s ease-in-out;
  transition: transform 1.1s ease-in-out, -webkit-transform 1.1s ease-in-out;
  transition-delay: 0.4s;
}


.container.sign-up-mode::before{
  -webkit-transform: translate(100%,-50%);
          transform: translate(100%,-50%);
  right: 52%;
}

.container.sign-up-mode .left-panel .image,
.container.sign-up-mode .left-panel .content
{
  -webkit-transform: translateX(-800px);
          transform: translateX(-800px);
}

.container.sign-up-mode form.sign-up-form{
  opacity: 1;
  z-index: 2;
}

.container.sign-up-mode form.sign-in-form{
  opacity: 0;
  z-index: 1;
}

.container.sign-up-mode .right-panel .image,
.container.sign-up-mode .right-panel .content{
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.container.sign-up-mode .left-panel{
  pointer-events: none;
}

.container.sign-up-mode .right-panel{
  pointer-events: all;
}

@media (max-width:570px){
  .signin-signup form{
    padding: 0 1.5rem;
  }

  .forms-container{
    top: 5%;
    left: 25%;
  }

  .image{
    display: none;
  }

  .panel .content{
    padding: 0.5rem 1rem;
  }

  .container{
    padding: 1.5rem;
  }

  .container:before{
    bottom: 72%;
    left: 50%;
  }

  .container.sign-up-mode::before{
    bottom: 28%;
    left:50%
  }
}

@media (min-width:570px) and (max-width:870px){
  .signin-signup,
  .container.sign-up-mode .signin-signup{
    left: 0%;
  }

}

@media (max-width:870px){
  .container{
    min-height: 800px;
    height: 100vh;
  }

  .signin-signup,
  .container.sign-up-mode .signin-signup{
    left: 20%;
  }

  .panels-container{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .panel{
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
  }

  .right-panel{
    grid-row: 3 / 4;
  }

  .left-panel{
    grid-row: 1 / 2;
  }

  .image{
    width: 200px;
    transition: -webkit-transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
    transition-delay: 0.8s;
  }

  .panel h3{
    font-size: 1.2rem;
  }

  .panel p{
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .button.transparent{
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }

  .container:before{
    width: 1500px;
    height: 1500px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: auto;
    right: initial;
    top: auto;
    top: initial;
    transition: 2s ease-in-out;
  }

  .container.sign-up-mode .left-panel .image,
  .container.sign-up-mode .left-panel .content{
    -webkit-transform: translateY(-300px);
            transform: translateY(-300px);
  }

  .container.sign-up-mode .right-panel .image,
  .container.sign-up-mode .right-panel .content{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }

  .right-panel .image,
  .right-panel .content{
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }

  .container.sign-up-mode .signin-signup{
    top: 5%;
    -webkit-transform: translate(-50%,0);
            transform: translate(-50%,0);
  }

}

